$blue: #1facc7;
$light_red: #ff5656;

@mixin btn_hover {
	&:hover {
		background-image: none;
		background-color: $blue; } }

* {
  margin: 0;
  padding: 0; }

html {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #272626;
  position: fixed;
  width: 100%;
  z-index: 999; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style: none; }

.menu_link {
	color: #fff;
	font-size: 15px;
	font-weight: 300; }

.menu_item {
	border-right: 2px dotted #000;
	padding-right: 50px;

	&:last-child {
      border-right: none; } }

.subheader {
  margin-top: 50px;
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);
  color: white; }

.subheader_logo {
	display: block;
	max-width: 100%;
	margin-top: 16px; }

.subheader_offitial {
	opacity: 0.5;
	color: #fff;
	font-size: 13px;
	font-weight: 400;
	margin-top: 6px; }

.subheader_call {
	color: #fff;
	font-size: 12px;
	font-weight: 300;
	margin-top: 19px;
	text-align: right; }

.subheader_phone {
	display: block;
	margin-top: 8px;
	color: #fff;
	font-size: 22px;
	font-weight: 400;
	text-transform: uppercase;
	text-align: right;
	line-height: 12px; }

.subheader_btn {
	margin-top: 19px;
	width: 172px;
	height: 46px;
	box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
	border: none;
	background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
	text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
	color: #fff;
	font-size: 14px;
	font-weight: 400;
	text-transform: uppercase;
	border-radius: 4px;
	@include btn_hover; }

.promo {
	padding: 150px 0 135px 0;
	min-height: 800px;
	background: url(../img/promo_bg.png) center (center / cover) no-repeat;
	&_header {
		margin: 0;
		margin-top: 40px;
		color: white;
		font-size: 60px;
		font-weight: 700;
		text-align: center;
		line-height: 60px; }
	&_subheader {
		margin: 0;
		margin-top: 22px;
		color: white;
		font-size: 36px;
		font-weight: 700;
		text-align: center;
		line-height: 36px; }
	&_descr {
		margin: 0;
		margin-top: 23px;
		color: white;
		font-size: 18px;
		font-weight: 400;
		text-align: center;
		line-height: 24px; }
	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 260px;
		height: 65px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #fff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		border-radius: 4px;
		@include btn_hover; } }

.title_header {
	display: block;
	margin: 0 auto;
	margin-top: 70px;
	width: 110px;
	height: 25px;
	font-size: 11px;
	font-weight: 300;
	color: white;
	background-color: black;
	text-align: center;
	padding-top: 3px; }
.title_subheader {
	margin: 0;
	margin-top: 40px;
	color: black;
	font-size: 38px;
	font-weight: 700;
	text-align: center;
	line-height: 38px; }

.advantages {
	min-height: 900px;
	&_pic {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: auto 25px auto 35px;
		width: 115px;
		height: 115px;
		background-color: $blue;
		border-radius: 50%;
		text-align: center; }
	&_tegs .col-md-6:nth-child(2n) .advantages_pic {
		margin: auto 25px auto 0; }
	&_tegs {
		display: flex;
		margin: 0;
		margin-top: 40px;
		padding-left: 45px; }
	&_descr {
		display: block;
		margin: 0;
		padding: 0;
		width: 340px;
		font-size: 15px;
		line-height: 21px;
		float: none;
		text-align: justify; }
	span {
		display: block;
		margin: 0;
		padding: 0;
		width: 340px;
		font-size: 15px;
		line-height: 21px;
		float: none;
		text-align: justify;
		color: $blue;
		font-size: 17px;
		line-height: 21px;
		float: none; }
	.col-md-6 {
		display: flex;
		padding-bottom: 35px; } }

.mobile {
	min-height: 864px;
	padding: 0 0 79px 0;
	background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
	&_text {
		margin-top: 19px;
		color: black;
		font-size: 17px;
		font-weight: 400;
		text-align: center; }
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/mobile_screen_1.png') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/mobile_screen_2.png') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/mobile_screen_3.png') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/mobile_screen_4.png') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/mobile_screen_5.png') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/mobile_screen_6.png') center (center / cover) no-repeat; }
		&_subtitle {
			color: white;
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			margin: 0 auto;
			background-color: #000;
			border-radius: 50%;
			padding: 6px;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $blue; }
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				display: block;
				width: 2px;
				height: 14px;
				background-color: $blue; }
			&:hover {
				&:before, &:after {
					background-color: $light_red;
					transition: 0.3 all; } } } } }

.choise {
	min-height: 896px;
	padding: 0 0 145px 0;
	background: url('../img/section_4_bg.png') center (center / cover) no-repeat;
	.title_header {
		color: black;
		background-color: white; }
	.title_subheader {
		color: white; }
	&_text {
		margin-top: 19px;
		color: white;
		font-size: 17px;
		font-weight: 400;
		text-align: center; }
	img {
		margin-top: 55px;
		transform: translateX(55px); }
	&_descr {
		font-size: 18px;
		font-weight: 400;
		text-align: center;
		color: #222; } }

.require {
	padding: 68px 0 84px0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0; } } }
